@import './mixins';

html {
    height: 100%;
}

body {
    width: 100%;
    height: 100%;

    .go-back-block {
        width: 400px;
        height: 300px;

        text-align: center;
        box-sizing: border-box;

        position: absolute;
        left: calc(50% - 200px);

        .title {
            font-size: 6rem;
        }

        .button {
            width: 120px;
            margin-top: 20px;
        }
    }

    &.lost-in-space {
        background-image: url(../img/404-lost-in-space-lg.svg);
        background-position: right bottom;
        background-color: #0B0929;
        background-size: contain;
        background-repeat: no-repeat;

        .go-back-block {
            bottom: 50px;
        }

        @media (max-width: 900px) {
            .go-back-block {
                top: 30px;
            }
        }

        @include sm-up-to {
            background-image: url(../img/404-lost-in-space-sm.svg);

            .go-back-block {
                top: calc(50% - 150px);
            }
        }
    }


    &.spaceship {
        background-image: url(../img/404-spaceship-lg.svg);
        background-position: center;
        background-color: #0B0929;
        background-size: contain;
        background-repeat: no-repeat;

        @include md-up-to {
            background-image: url(../img/404-spaceship-md.svg);
        }

        .go-back-block {
            bottom: 130px;
        }
    }

}